$font-family: 'Lato', sans-serif;
$bg-color: #DBE9EE;
$lore-width: 500px;
$lore-height: 150px;
$lore-border: 1px solid black;

html{

}

header{
  background-color: $bg-color;
}

ul{
  list-style: none;
  margin: 0;
  padding-left: 0;
}

